import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      component: () => import('@/views/dashboard/Index'),
      children: [
        // Dashboard
        {
          name: '满意度标注',
          path: '',
          component: () => import('@/views/dashboard/Login'),
        },
        {
          name: '说明',
          path: 'instruction',
          component: () => import('@/views/dashboard/Instruction'),
        },
        {
          name: '任务',
          path: 'question',
          component: () => import('@/views/dashboard/SessSatifQuestion'),
        },
        {
          name: '谢谢',
          path: 'thanks',
          component: () => import('@/views/dashboard/Thanks'),
        },
      ],
    },
  ],
})
